@use 'base/variables' as v;@use 'base/mixins' as m;.lista-servicios {  display: grid;  gap: 2rem;  @include m.tablet {    @include m.grid(2, 2rem);  }  .servicio {    background-color: v.$blanco;    border-radius: 8px;    border: none;    text-align: center;    padding: 3rem;    transition-property: transform;    transition-duration: 300ms;    cursor: pointer;    &.seleccionado {      background-color: v.$azul;      p {        color: v.$blanco;      }      .precio-servicio {        color: v.$blanco;      }    }    &:hover {      transform: scale(1.1);    }    p {      color: v.$negro;      font-weight: v.$regular;      margin: 0 0 calc(v.$separacion / 4) 0;    }    .precio-servicio {      color: v.$azul;      font-weight: v.$bold;      font-size: 2.5rem;      margin: 0;      @include m.tablet {        font-size: 3rem;      }    }  }}